 .wp-block-harvestworks-event-calendar {
	
	margin: 0px auto;

	#calendar {
		user-select: none;
		display: grid;
		grid-template-columns: repeat(7, 1fr);
		grid-template-rows: repeat(6, auto);
		margin-top: -0.4rem;

		#calendar_body {
			grid-column-start: 1;
			grid-column-end: 8;
			display: grid;
			grid-template-columns: repeat(7, 1fr);
			grid-template-rows: repeat(auto, 1fr);
			outline: 1px solid lightgrey;
			gap: 1px;

			>div {
				outline: 1px solid lightgrey;
			}
		}

		>div {
			display: flex;
			justify-content: center;
			align-items: center;
		}

		#calendar_date {
			justify-content: left;
			margin-bottom: 1.2rem;

			>* {
				margin: 0;
				font-family: inherit;
				font-weight: 500;
			}
		}

		button {
			aspect-ratio: 1;
			cursor: pointer;
			border: none;
			overflow: auto;
			background: none;

			display: flex;
			justify-content: center;
			align-items: center;

			padding: 0.6rem;
			font-size: 1.2rem;
		}

		.calendar_weekday {
			text-align: center;
			outline: none;
			flex: 1;
			padding: 10px;
			text-wrap: nowrap;
		}

		.calendar_day {
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			aspect-ratio: .9;
		}

		.calendar_day.has_event::after {
			content: "";
			position: absolute;
			left: 42.5%;
			bottom: 12.5%;
			display: block;
			background-color: var(--hw-event-calendar-has-event-color);
			border-radius: 50%;
			width: 15%;
			aspect-ratio: 1;
		}

		.calendar_day.queried {
			background-color: var(--hw-event-calendar-queried-day-color);
			box-shadow: 0px 0px 15px var(--hw-event-calendar-queried-day-color);

			z-index: 1;
			color: white;
			font-weight: 600;

			transition: background-color .3s ease, box-shadow .3s ease;
		}
	}
}